import React from "react"
import axios from "axios"
import qs from "qs"
import './App.css'
import Restaurants from "./Restaurants.js"
import Map from "./Map.js"
import Options from "./Options.js"


function App() {

  // shop 
  const [shops, setShops] = React.useState([]);

  // 
  const [isSearching, setIsSearching] = React.useState(false)

  // option
  const [options, setOptions] = React.useState({
    wifi: 0,
    card: 0,
    wine: 0,
    nihonsyu: 0,
    free_drink: 0,
    free_food: 0,
    budgetLower: 0,
    budgetUpper: 99999,
    genreSet: new Set(),
    order: 4,
    range: 4,
  });

  // optionsupdate
  // options: {wifi: 0, wine: 1}
  const updateOptions = (optionsDiff) => {
    let newOptions =  { ...options};
    for (let key in optionsDiff) {
      newOptions[key] = optionsDiff[key];
    }
    setOptions(newOptions);
    console.log(newOptions);
  }

  // zoom  range
  // * when (zoom <= 13) : range = 5max
  // * when (13 <= zoom <= 17) : 1 <= range <= 5 (1)
  // * when (zoom == 18) : range = 1 (min)
  const calcRangeFromZoom = (zoom) => {

    let range;
    if (zoom < 13) {
      range = 5;
    } else {
      range = 17 - zoom;
    }

    if (range <= 0) {
      range = 1;
    }

    return range;
  }


  // axios
  const myAPI = axios.create ({
    baseURL: 'https://fathomless-wave-02848.herokuapp.com/hotpepper/'
  })

  // herokuAPI
  const fetchSearchData = async(params) => {
    return await myAPI.get('', {
      params: params,
      paramsSerializer: (params) => {
        return qs.stringify(params, {arrayFormat: 'repeat'});
      }
    })
  }

  // c = {lat: , lng: }
  // c shops
  const searchRestaurants = (c) => {

    const params = {
      ...options,
      genre: Array.from(options.genreSet),
      lat: c.lat,
      lng: c.lng,
    }

    fetchSearchData(params).then((res) => { 
      setShops(res["data"]);
      console.log(res);
      setIsSearching(false); // OFF
    })
  }

  return (
      <div className="app-container">
        <Restaurants 
          shops = {shops}
        />
        <Map 
          handleClick = {(c) => {
            setIsSearching(true); 
            searchRestaurants(c);
          }}
          handleZoomChange = {(zoom) => {updateOptions({range: calcRangeFromZoom(zoom)})}}
          shops = {shops}
          isSearching = {isSearching}
        />
        <Options
          genreSet = {options.genreSet}
          updateOptions = {(optionsDiff) => updateOptions(optionsDiff)}
        />
      </div>
  );
}

export default App;